import React, { useState } from 'react'

import Datetime from 'react-datetime'
import "react-datetime/css/react-datetime.css";

import { SelectUi } from '../selectUi/SelectUi'
import { state } from '../selectUi/dataFormat/state'
import { department } from '../selectUi/dataFormat/department'

// import Inputs from './inputCreator'

import { useDispatch, useSelector } from 'react-redux';
import { getForm, setDatas, setModal } from './formSlice'


function EmployeeForm() {

    const [birthDay, changeBirth] = useState(new Date())
    const [startDay, changeStart] = useState(new Date())

    const dispatch = useDispatch();
    const formDatas = useSelector((state)=> state.form)

    const saveEmployee = (e) => {
        e.preventDefault();

        const inputs = Array.from(e.target);

        // create new data object 
        inputs.forEach((input, index) => {
            // exclude fielset
            if (index === 4) return;
            // exclude button
            if (input.value === '') return;
            // set form datas to data pool
            dispatch(getForm({ cellValue: input.value, category: input.id }));
        })
        // form validation to set datas
        if (formDatas.length === 9) {
            dispatch(setDatas(formDatas));
            dispatch(setModal());
        }
    }

    return (
        <form id='create-employee' onSubmit={saveEmployee}>
            {/* <Inputs /> */}
            <div className='inputWrap'>
                <label htmlFor='firstName'>First Name</label>
                <input type='text' id='firstName' />
            </div>
            <div className='inputWrap'>
                <label htmlFor='lastName'>Last Name</label>
                <input type='text' id='lastName' />
            </div>
            <div className='inputWrap'>
                <label htmlFor='dateOfBirth'>Date of Birth</label>
                <Datetime
                    inputProps={{ id: "dateOfBirth" }}
                    timeFormat={false}
                    onChange={changeBirth}
                    value={birthDay}
                />
            </div>
            <div className='inputWrap'>
                <label htmlFor='start-date'>Start Date</label>
                <Datetime
                    inputProps={{ id: "startDate" }}
                    timeFormat={false}
                    onChange={changeStart}
                    value={startDay}
                />
            </div>
            <fieldset className='address'>
                <legend>Address</legend>

                <div className='inputWrap'>
                    <label htmlFor="street">Street</label>
                    <input id="street" type="text" />
                </div>
                <div className='inputWrap'>
                    <label htmlFor="city">City</label>
                    <input id="city" type="text" />
                </div>
                <div className='inputWrap'>
                    <SelectUi
                        name='state'
                        options={state}
                    />
                </div>
                <div className='inputWrap'>
                    <label htmlFor="zipCode">Zip Code</label>
                    <input id="zipCode" type="number" />
                </div>
            </fieldset>
            <div className='inputWrap'>
                <SelectUi
                    name='department'
                    options={department}
                />
            </div>
            <br />
            <button className='submit-button'>Save</button>
        </form>
    )
}

export default EmployeeForm